import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import CreateAcc from '../views/CreateAcc.vue'
import UserSelection from '../views/UserSelection.vue'
import UserProfile from '../views/UserProfile.vue'
import ShareYourCar from '../views/ShareYourCar.vue'
import RentAcar from '../views/RentAcar.vue'
import payment from '../views/payment.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/payment',
    name: 'payment',
    component: payment
  },
  {
    path: '/RentAcar',
    name: 'RentAcar',
    component: RentAcar
  },
  {
    path: '/ShareYourCar',
    name: 'ShareYourCar',
    component: ShareYourCar
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  },
  {
    path: '/CreateAcc',
    name: 'CreateAcc',
    component: CreateAcc
  },
  {
    path: '/UserSelection',
    name: 'UserSelection',
    component: UserSelection
  },
  {
    path: '/userprofile',
    name: 'userProfile',
    component: UserProfile
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
