const state ={
  user:null
}
const getters ={
    getUser(state){
        return state.user;
    }
}

const mutations ={
    toLogin(state,payload){
        localStorage.setItem('AuthKey','EralpsSoftware')
        state.user= payload
    },
    clearUser(state){
            state.user=null
    }
}

const actions ={

}

export const user = {
    state,getters,mutations,actions
}
