  watch: {
    choosing() {
      this.active++;
    }
  },
      computed:{
    ...mapGetters(['getCategories','getCars','getCities','getMaker','getCity','getUser'])
  },
  methods: {
               rentaCar(){
                 console.log(this.id);
                 console.log(this.getCars);
                 console.log(this.getCars[this.id]);
                 
                 if(this.filter){
                   console.log(this.filtredCar[this.id]);
                      this.$store.commit('clearRentAcar')
              
                  this.$store.commit('rentAcar',this.filtredCar[this.id])   
                  
                 this.$router.push('/payment')
                 }
                 else{
                   console.log(this.getCars[this.id]);
                      this.$store.commit('clearRentAcar')
              
                     this.$store.commit('rentAcar',this.getCars[this.id])
                            this.$router.push('/payment')
                 }
              
             
             
              
           },
    carsFiltred(filterName) {
      console.log('filterName Markaya gre');
      if(this.getCategories!=null){
        this.filtredCar=[]
      this.filter = true;

      
      
      for (let i = 0; i < this.getCategories.length; i++) {
     this.getCars.filter(res => {
        if (res.make == filterName[i]) {
          this.filtredCar.push(res)
        }
      });

      }   
               this.$store.commit('clearCategories')
      }
      else{
             this.filter = true;
  this.filtredCar = this.getCars.filter(res => {
        if (res.make == filterName) {
          return res
        }
      });
      }

    
    },
    show(value){
       this.map=true
      this.id=value
      
    },    addCar() {
      this.active++;
      setTimeout(() => {
        this.active++;
      }, 1000);
    },
    carsFiltredCity(filterName){
        console.log('filterName ehire gre');
      if(this.getCity!=null){
        this.filtredCar=[]
      this.filter = true;
      for (let i = 0; i < this.getCity.length; i++) {
     this.getCars.filter(res => {
        if (res.city == filterName[i]) {
          this.filtredCar.push(res)
        }
      });

      }   
               this.$store.commit('clearCity')
      }
      else{
        console.log('worked');
        
             this.filter = true;
  this.filtredCar = this.getCars.filter(res => {
        if (res.city == filterName) {
          return res
        }
      });
      }
    },
  cardFiltredCityCategory(filterNameMaker,filterNameCity){
   console.log('ikiside');
         console.log('category ', this.getCategories);
      console.log('city' , this.getCity);

      if(this.getCity!=null){
        this.filtredCar=[]
      this.filter = true;
      for (let i = 0; i < this.getCity.length; i++) {
        for (let j = 0; j < this.getCategories.length; j++) {
     this.getCars.filter(res => {
       if(res.make==filterNameMaker[j] && res.city == filterNameCity[i]){
         this.filtredCar.push(res)
         
       }
        
      });
        }
      }   
               this.$store.commit('clearCity')
               this.$store.commit('clearCategories')

      }
  },
    handleChange(value) {
      this.choosing = value;
    },
    acceptCar() {
      this.active++;
      this.step = false;
    },
    back() {
      this.active--;
      this.step = true;
    },
    emptyCar() {
      this.choosing = null;
      this.active--;
    }
  },
  mounted() {
    console.log(this.getCategories);
      if(this.getCategories.length!=0 && this.getCity.length!=0){
        this.cardFiltredCityCategory(this.getCategories,this.getCity)
      
    }
   else if(this.getCategories.length!=0 ){
      this.carsFiltred(this.getCategories)
    }
    else if(this.getCity.length!=0){
        this.carsFiltredCity(this.getCity)
    }
  
  },

};
</script>

